<template>
  <div class="home" style ="padding: 10px">

    <!-- -->
    <div style="margin: 10px 0;">
      <el-form inline="true" size="small">
        <el-form-item label="" >
          <el-input v-model="search1" placeholder=""  clearable>
            <template #prefix><el-icon class="el-input__icon"><search/></el-icon></template>
          </el-input>
        </el-form-item >
        <el-form-item label="" >
          <el-input v-model="search2" placeholder=""  clearable>
            <template #prefix><el-icon class="el-input__icon"><search /></el-icon></template>
          </el-input>
        </el-form-item >
        <el-form-item label="" >
          <el-input v-model="search3" placeholder=""  clearable>
            <template #prefix><el-icon class="el-input__icon"><search /></el-icon></template>
          </el-input>
        </el-form-item >
        <el-form-item>
          <el-button type="primary" style="margin-left: 1%" @click="load" size="mini" >
            <svg-icon iconClass="search"/></el-button>
        </el-form-item>
        <el-form-item>
          <el-button size="mini"  type="danger" @click="clear"></el-button>
        </el-form-item>
        <el-form-item style="float: right" v-if="numOfOutDataBook!=0">
          <el-popconfirm
              confirm-button-text=""
              cancel-button-text=""
              :icon="InfoFilled"
              icon-color="red"
              title=""
              @confirm="toLook"
          >
            <template #reference>
              <el-button  type="warning"></el-button>
            </template>
          </el-popconfirm>
        </el-form-item>
      </el-form>
    </div>
    <!-- -->
    <div style="margin: 10px 0;" >
      <el-button type="primary" size="mini" @click = "add" v-if="user.role === 1" style="margin-right: 10px"></el-button>
      <el-popconfirm title="?" @confirm="deleteBatch" v-if="user.role === 1">
        <template #reference>
          <el-button type="danger" size="mini" ></el-button>
        </template>
      </el-popconfirm>
    </div>
    <!-- -->
    <el-table :data="tableData" stripe border="true" @selection-change="handleSelectionChange">
      <el-table-column v-if="user.role === 1"
                       type="selection"
                       width="55">
      </el-table-column>
      <el-table-column prop="id" label="" sortable />
      <el-table-column prop="title" label="" />
      <el-table-column prop="author" label="" />
      <el-table-column prop="available_qty" label="" sortable />
      <el-table-column fixed="right" label="" >
        <template v-slot="scope">
          <el-button  size="mini" @click ="handleEdit(scope.row)" v-if="user.role === 1"></el-button>
          <el-popconfirm title="?" @confirm="handleDelete(scope.row.id)" v-if="user.role === 1">
            <template #reference>
              <el-button type="danger" size="mini" ></el-button>
            </template>
          </el-popconfirm>
          <el-button  size="mini" @click ="handlelend(scope.row.id,scope.row.isbn,scope.row.name,scope.row.borrownum)" v-if="user.role == 0" :disabled="scope.row.status == 0"></el-button>
          <el-popconfirm title="?" @confirm="handlereturn(scope.row.id,scope.row.isbn,scope.row.borrownum)" v-if="user.role == 0" :disabled="scope.row.status == 1">
            <template #reference>
              <el-button type="danger" size="mini" :disabled="(this.isbnArray.indexOf(scope.row.isbn)) == -1 ||scope.row.status == 1" ></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
<!--,-->
    <el-dialog
        v-model="dialogVisible3"
        v-if="numOfOutDataBook!=0"
        title=""
        width="50%"
        :before-close="handleClose"
    >
        <el-table :data="outDateBook" style="width: 100%">
          <el-table-column prop="isbn" label="" />
          <el-table-column prop="bookName" label="" />
          <el-table-column prop="lendtime" label="" />
          <el-table-column prop="deadtime" label="" />
        </el-table>

      <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="dialogVisible3 = false"
        ></el-button>
      </span>
      </template>
    </el-dialog>
    <!--    -->
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total"
          @current-change="handleCurrentChange"
      >
      </el-pagination>

      <el-dialog v-model="dialogVisible" title="" width="30%">
        <el-form :model="form" label-width="120px">

          <el-form-item label="">
            <el-input style="width: 80%" v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 80%" v-model="form.author"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 80%" v-model.number="form.available_qty" type="number" min="0"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </span>
        </template>
      </el-dialog>

      <el-dialog v-model="dialogVisible2" title="" width="30%">
        <el-form :model="form" label-width="120px">

          <el-form-item label="">
            <el-input style="width: 80%" v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 80%" v-model="form.author"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input style="width: 80%" v-model.number="form.available_qty" type="number" min="0"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </span>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import request from "../utils/request";
import {ElMessage} from "element-plus";
import moment from "moment";

// 
const mockBooks = [
  {
    id: 1,
    title: 'Java ',
    author: '',
    available_qty: 5
  },
  {
    id: 2,
    title: 'Vue3 ',
    author: '',
    available_qty: 2
  },
  {
    id: 3,
    title: '',
    author: '',
    available_qty: 10
  }
]

export default {
  created(){
    const raw = sessionStorage.getItem("user") || localStorage.getItem("user") || "{}"
    this.user = JSON.parse(raw)
    //  role 
    if (this.user.role !== undefined && this.user.role !== null) {
      this.user.role = Number(this.user.role)
    }
    console.log("User data:", this.user)
    console.log("User role:", this.user.role, "Type:", typeof this.user.role)
    this.load()
  },
  name: 'BookView',
  methods: {
  // (this.isbnArray.indexOf(scope.row.isbn)) == -1
    handleSelectionChange(val){
      this.ids = val.map(v =>v.id)
    },
    deleteBatch(){
      if (!this.ids.length) {
        ElMessage.warning("")
        return
      }
      // 
      if (this.useMock) {
        this.ids.forEach(id => {
          const index = mockBooks.findIndex(b => b.id === id)
          if (index !== -1) {
            mockBooks.splice(index, 1)
          }
        })
        ElMessage.success("")
        this.load()
        return
      }
      //  /delById?id=...
      Promise.all(
        this.ids.map(id =>
          request.get("/delById", { params: { id } })
        )
      ).then(() => {
        ElMessage.success("")
        this.load()
      }).catch(() => {
        ElMessage.error("")
      })
    },
    load(){
      // 
      if (this.useMock) {
        let list = mockBooks.filter(item => {
          const matchId = this.search1 ? String(item.id).includes(this.search1) : true
          const matchTitle = this.search2 ? (item.title || '').includes(this.search2) : true
          const matchAuthor = this.search3 ? (item.author || '').includes(this.search3) : true
          return matchId && matchTitle && matchAuthor
        })
        this.total = list.length
        const start = (this.currentPage - 1) * this.pageSize
        const end = start + this.pageSize
        this.tableData = list.slice(start, end)
        return
      }

      // 
      const params = {
        page: this.currentPage,
        pageSize: this.pageSize,
      }

      //  /books
      let url = "/books"
      if (this.search2 && this.search2.trim()) {
        url = "/findByTitle"
        params.keyword = this.search2.trim()
      } else if (this.search3 && this.search3.trim()) {
        url = "/findByAuthor"
        params.author = this.search3.trim()
      }

      request.get(url, { params }).then(res => {
        let listData = []
        const payload = res.data

        if (Array.isArray(payload)) {
          listData = payload
        } else if (payload && Array.isArray(payload.records)) {
          listData = payload.records
        } else if (payload && Array.isArray(payload.rows)) {
          listData = payload.rows
        } else if (payload) {
          listData = payload
        }

        if (!Array.isArray(listData)) {
          listData = []
        }

        // 
        const mapped = listData.map(item => ({
          id: item.id,
          title: item.title || item.name || item.bookName || '',
          author: item.author || '',
          available_qty: item.available_qty != null ? item.available_qty : (item.borrownum != null ? item.borrownum : 0),
          borrownum: item.borrownum != null ? item.borrownum : 0,
          status: (typeof item.status === 'string' ? Number(item.status) : (item.status != null ? item.status : 1)),
          isbn: item.isbn || ''
        }))

        // //
        const filtered = mapped.filter(item => {
          const matchId = this.search1 ? String(item.id || '').includes(this.search1.trim()) : true
          const matchTitle = this.search2 ? (item.title || '').includes(this.search2.trim()) : true
          const matchAuthor = this.search3 ? (item.author || '').includes(this.search3.trim()) : true
          return matchId && matchTitle && matchAuthor
        })

        this.tableData = filtered
        this.total = (payload && payload.total) ? payload.total : filtered.length
      }).catch(err => {
        console.error(":", err)
        ElMessage.error("")
        this.tableData = []
        this.total = 0
      })
    },
    clear(){
      this.search1 = ""
      this.search2 = ""
      this.search3 = ""
      this.currentPage = 1
      this.$nextTick(() => {
        this.load()
      })
    },

    handleDelete(id){
      // 
      if (this.useMock) {
        const index = mockBooks.findIndex(b => b.id === id)
        if (index !== -1) {
          mockBooks.splice(index, 1)
          ElMessage.success("")
          this.load()
        } else {
          ElMessage.error("")
        }
        return
      }

      //  /delById?id=...
      request.get("/delById", { params: { id } }).then(res =>{
        console.log(res)
        if(res.code === 1 || res.code === 0 ){
          ElMessage.success(res.msg || "")
        }
        else {
          ElMessage.error(res.msg || "")
        }
        this.load()
      })
    },
    handlereturn(id,isbn,bn){
      this.form.status = "1"
      this.form.id = id
      request.put("/book",this.form).then(res =>{
        console.log(res)
        if(res.code == 1){
          ElMessage({
            message: '',
            type: 'success',
          })
        }
        else {
          ElMessage.error(res.msg)
        }
        this.form3.isbn = isbn
        this.form3.readerId = this.user.id
        let endDate = moment(new Date()).format("yyyy-MM-DD HH:mm:ss")
        this.form3.returnTime = endDate
        this.form3.status = "1"
        this.form3.borrownum = bn
        request.put("/LendRecord1/",this.form3).then(res =>{
          console.log(res)
          let form3 ={};
          form3.isbn = isbn;
          form3.bookName = name;
          form3.nickName = this.user.username;
          form3.id = this.user.id;
          form3.lendtime = endDate;
          form3.deadtime = endDate;
          form3.prolong  = 1;
          request.post("/bookwithuser/deleteRecord",form3).then(res =>{
            console.log(res)
            this.$nextTick(() => {
              this.load()
            })
          })
        })
      })
    },
    handlelend(id,isbn,name,bn){
      if(this.number ==5){
        ElMessage.warning("")
        return;
      }
      if(this.numOfOutDataBook !=0){
        ElMessage.warning("")
        return;
      }
      this.form.status = "0"
      this.form.id = id
      this.form.borrownum = bn+1
      request.put("/book",this.form).then(res =>{
        if(res.code == 1){
          ElMessage({
            message: '',
            type: 'success',
          })
        }
        else {
          ElMessage.error(res.msg)
        }
      })

      this.form2.status = "0"
      this.form2.isbn = isbn
      this.form2.bookname = name
      this.form2.readerId = this.user.id
      this.form2.borrownum = bn+1
      let startDate = moment(new Date()).format("yyyy-MM-DD HH:mm:ss");
      this.form2.lendTime = startDate
      request.post("/LendRecord",this.form2).then(res =>{
        console.log(res)
      })

      let form3 ={};
      form3.isbn = isbn;
      form3.bookName = name;
      form3.nickName = this.user.username;
      form3.id = this.user.id;
      form3.lendtime = startDate;
      let nowDate = new Date(startDate);
      nowDate.setDate(nowDate.getDate()+30);
      form3.deadtime = moment(nowDate).format("yyyy-MM-DD HH:mm:ss");
      form3.prolong  = 1;
      request.post("/bookwithuser/insertNew",form3).then(res =>{
        console.log(res)
        this.$nextTick(() => {
          this.load()
        })
      })
    },
    add(){
      this.dialogVisible= true
      this.form ={
        available_qty: 0
      }
    },
    save(){
      //  / 
      if (this.useMock) {
        if(this.form.id){
          const index = mockBooks.findIndex(b => b.id === this.form.id)
          if (index !== -1) {
            mockBooks.splice(index, 1, { ...mockBooks[index], ...this.form })
            ElMessage.success('')
          } else {
            ElMessage.error('')
          }
        }
        else {
          const maxId = mockBooks.reduce((max, item) => Math.max(max, item.id), 0)
          const newItem = {
            ...this.form,
            id: maxId + 1,
          }
          mockBooks.push(newItem)
          ElMessage.success('')
        }
        this.$nextTick(() => {
          this.load()
          this.dialogVisible = false
          this.dialogVisible2 = false
        })
        return
      }

      //  id /
      if(this.form.id){
        // 
        request.post("/updateBook",{
          id: this.form.id,
          available_qty: this.form.available_qty
        }).then(res =>{
          console.log(res)
          if(res.code === 1 || res.code === 0){
            ElMessage({
              message: res.msg || '',
              type: 'success',
            })
          }
          else {
            ElMessage.error(res.msg || '')
          }
          this.$nextTick(() => {
            this.load()
            this.dialogVisible2 = false
          })
        })
      }
      else {
        // 
        request.post("/addBook",{
          title: this.form.title,
          author: this.form.author,
          available_qty: this.form.available_qty
        }).then(res =>{
          console.log(res)
          if(res.code === 1 || res.code === 0){
            ElMessage.success(res.msg || '')
          }
          else {
            ElMessage.error(res.msg || '')
          }
          this.$nextTick(() => {
            this.load()
            this.dialogVisible = false
          })
        })
      }

    },
    // formatter(row) {:formatter="formatter"
    //   return row.address
    // },

    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible2 = true
    },
    handleSizeChange(pageSize){
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.currentPage = pageNum
      this.$nextTick(() => {
        this.load()
      })
    },
    toLook(){
      this.dialogVisible3 =true;
    },
  },
  data() {
    return {
      form: {},
      form2:{},
      form3:{},
      dialogVisible: false,
      dialogVisible2: false,
      search1:'',
      search2:'',
      search3:'',
      total:10,
      currentPage:1,
      pageSize: 5,
      tableData: [],
      user:{},
      number:0,
      bookData:[],
      isbnArray:[],
      outDateBook:[],
      numOfOutDataBook: 0,
      dialogVisible3 : true,
      //  false 
      useMock: false,
    }
  },
}
</script>
